import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import IconButton from "@material-ui/core/IconButton";
import EBData from "./evangelionBookData.json";
import Grid from "@material-ui/core/Grid";
import { Container } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { AddCircleOutline, SearchOutlined } from "@material-ui/icons";
import Modal from "@material-ui/core/Modal";

// BookList
// 

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: "100%",
    height: 350,
    // paddingTop: 5,
    // marginTop: 5,
  },
  icon: {
    color: "rgba(255, 255, 255, 0.6)",
  },
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "1px solid #000",
    boxShadow: theme.shadows[0],
    padding: theme.spacing(1, 1, 1),
  },
}));

function getModalStyle() {
  const top = 50;
  const left = 50;
  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

/**
 * 
 */
const R082GridList = () => {
  const classes = useStyles();

  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  /**
   * 
   * @param {*} books
   */
  const extractBooks = (books) => {
    const items = books.items;
    const result = items.map((item) => {
      return {
        title: item.volumeInfo.title,
        author: item.volumeInfo.authors
          ? item.volumeInfo.authors.join(", ")
          : "",
        img: item.volumeInfo.imageLinks
          ? item.volumeInfo.imageLinks.smallThumbnail
          : "",
      };
    });
    return result;
  };

  /**
   * 
   */
  const bookList = extractBooks(EBData);

  /**
   * 
   */
  const searchJSX = (
    <Grid container>
      <Grid item xs={8}>
        <div>
          <TextField
            label=""
            defaultValue=""
            variant="outlined"
            size="small"
            fullWidth
          />
        </div>
        <div style={{ marginTop: 8 }}>
          <TextField
            label=""
            defaultValue=""
            variant="outlined"
            size="small"
            fullWidth
          />
        </div>
      </Grid>
      <Grid item container xs={4} justify="flex-end">
        <div>
          <Button variant="outlined" size="large">
            <SearchOutlined /> 
          </Button>
        </div>
      </Grid>
    </Grid>
  );

  /**
   * List
   */
  const bookListJSX = (
    <Grid container>
      <GridList cellHeight={120} className={classes.gridList}>
        <GridListTile
          key="Subheader"
          cols={2}
          style={{ height: "auto" }}
        ></GridListTile>
        {bookList.map((tile, idx) => (
          <GridListTile key={idx.toString()}>
            <img src={tile.img} alt={tile.title} />
            <GridListTileBar
              title={tile.title}
              subtitle={<span>{tile.author}</span>}
              actionIcon={
                <IconButton
                  aria-label={`info about ${tile.title}`}
                  className={classes.icon}
                >
                  <AddCircleOutline />
                </IconButton>
              }
            />
          </GridListTile>
        ))}
      </GridList>
    </Grid>
  );

  const bodyJSX = (
    <div style={modalStyle} className={classes.paper}>
      <Container fixed className="">
        {/*  */}
        {searchJSX}
        {/* List */}
        {bookListJSX}
      </Container>
    </div>
  );

  return (
    <div className={classes.root}>
      <button type="button" onClick={handleOpen}>
        Open Modal
      </button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {bodyJSX}
      </Modal>
    </div>
  );
};

export default R082GridList;
