import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import IconButton from "@material-ui/core/IconButton";
import EBData from "./evangelionBookData.json";
import Grid from "@material-ui/core/Grid";
import { Container } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { AddCircleOutline, SearchOutlined } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: "100%",
    height: 350,
  },
  icon: {
    color: "rgba(255, 255, 255, 0.6)",
  },
}));

/**
 * 
 */
const R081GridList = () => {
  const classes = useStyles();

  /**
   * 
   * @param {*} books
   */
  const extractBooks = (books) => {
    const items = books.items;
    const result = items.map((item) => {
      return {
        title: item.volumeInfo.title,
        author: item.volumeInfo.authors
          ? item.volumeInfo.authors.join(", ")
          : "",
        img: item.volumeInfo.imageLinks
          ? item.volumeInfo.imageLinks.smallThumbnail
          : "",
      };
    });
    return result;
  };

  /**
   * 
   */
  const bookList = extractBooks(EBData);

  return (
    <div className={classes.root}>
      <Container fixed className="">
        <Grid container>
          <Typography variant="h6"></Typography>
        </Grid>
        <Grid container>
          <Grid item xs={8}>
            <div>
              <TextField
                label=""
                defaultValue=""
                variant="outlined"
                size="small"
                fullWidth
              />
            </div>
            <div>
              <TextField
                label=""
                defaultValue=""
                variant="outlined"
                size="small"
                fullWidth
              />
            </div>
          </Grid>
          <Grid item container xs={4} justify="flex-end">
            <div>
              <Button variant="outlined" size="large">
                <SearchOutlined /> 
              </Button>
            </div>
          </Grid>
        </Grid>

        {/* List */}
        <Grid container>
          <GridList cellHeight={150} className={classes.gridList}>
            <GridListTile
              key="Subheader"
              cols={2}
              style={{ height: "auto" }}
            ></GridListTile>
            {bookList.map((tile, idx) => (
              <GridListTile key={idx.toString()}>
                <img src={tile.img} alt={tile.title} />
                <GridListTileBar
                  title={tile.title}
                  subtitle={<span>{tile.author}</span>}
                  actionIcon={
                    <IconButton
                      aria-label={`info about ${tile.title}`}
                      className={classes.icon}
                    >
                      <AddCircleOutline />
                    </IconButton>
                  }
                />
              </GridListTile>
            ))}
          </GridList>
        </Grid>
      </Container>
    </div>

    // <div>
    //   <Container fixed className="bg-c-lightyellow">
    //     <p className="m22">Grid Normal</p>
    //     <Grid container>
    //       <Grid item xs={6} className="bg-c-sky">
    //         xs=6
    //       </Grid>
    //       <Grid item xs={6} className="bg-c-pink">
    //         xs=6
    //       </Grid>
    //     </Grid>
    //     <hr />
    //     <Grid container>
    //       <Grid item xs={3} className="bg-c-sky">
    //         xs=3
    //       </Grid>
    //       <Grid item xs={3} className="bg-c-pink">
    //         xs=3
    //       </Grid>
    //       <Grid item xs={3} className="bg-c-sky">
    //         xs=3
    //       </Grid>
    //       <Grid item xs={3} className="bg-c-pink">
    //         xs=3
    //       </Grid>
    //     </Grid>
    //     <hr />
    //   </Container>
    // </div>
  );
};

export default R081GridList;
