import React, { useState } from "react";
import { Container, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Switch from "@material-ui/core/Switch";
import { ControlCamera } from "@material-ui/icons";
import TextField from "@material-ui/core/TextField";
import AccountCircle from "@material-ui/icons/AccountCircle";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import FavoriteIcon from "@material-ui/icons/Favorite";
import NavigationIcon from "@material-ui/icons/Navigation";
import Badge from "@material-ui/core/Badge";
import MailIcon from "@material-ui/icons/Mail";
import IconButton from "@material-ui/core/IconButton";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import { pink } from "@material-ui/core/colors";

// material-ui
// form

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  good_btn: {
    border: "1px solid pink",
    padding: "1px 1px",
    color: "hotpink",
    fontWeight: 600,
    // fontSize: "16px",
    minWidth: "45px",
  },
  uname: {
    fontWeight: 600,
  },
}));

/**
 * 
 */
const R053Main = () => {
  const classes = useStyles((theme) => ({}));

  // good
  const [good, setGood] = useState(0);

  /**
   * Good
   */
  const handleGoodClick = () => {
    setGood(good + 1);
  };

  return (
    <div>
      <Container fixed className=" height200vh">
        <h2 className="m22">typography h2</h2>
        <hr />
        <Typography variant="body2">
          1.&nbsp;
          <Typography variant="inherit" className={classes.uname}>
            
          </Typography>
          2020/08/21 23:04:15 &nbsp;
          <Button
            variant="outlined"
            color="secondary"
            size="small"
            className={classes.good_btn}
            onClick={handleGoodClick}
          >
            <FavoriteIcon />
            &nbsp;{good}
          </Button>
        </Typography>

        <hr />
        <p></p>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Grid container>&nbsp;</Grid>
            <Grid container>
              <Grid item xs={1}>
                &nbsp;
              </Grid>
              <Grid item xs={10}>
                <TextField
                  id="outlined-basic"
                  fullWidth
                  size="small"
                  label=""
                  placeholder=""
                  variant="outlined"
                />
                <Grid container>&nbsp;</Grid>
                <TextField
                  id="outlined-multiline-static"
                  fullWidth
                  // size="small"
                  label=""
                  multiline
                  rows={4}
                  variant="outlined"
                />
              </Grid>
              <Grid item xs={1}>
                &nbsp;
              </Grid>
            </Grid>
            <Grid container>&nbsp;</Grid>
            <Grid container>
              <Grid item xs={1}>
                &nbsp;
              </Grid>
              <Grid item container xs={10} justify="flex-end">
                <Button variant="outlined" size="small">
                  
                </Button>
              </Grid>
              <Grid item xs={1}>
                &nbsp;
              </Grid>
            </Grid>
            <Grid container>&nbsp;</Grid>
          </Paper>
        </Grid>

        <hr />
        <p></p>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Grid container>&nbsp;</Grid>
            <Grid container>
              <Grid item xs={1}>
                &nbsp;
              </Grid>
              <Grid container item xs={10}>
                <Grid item xs={8}>
                  <TextField
                    id="outlined-basic"
                    fullWidth
                    size="small"
                    label=""
                    placeholder=""
                    variant="outlined"
                  />
                </Grid>
                <Grid item container xs={4} justify="flex-end">
                  <Button variant="outlined"></Button>
                </Grid>
                {/* <Grid item xs={4}> */}
                {/* <Grid item container xs={10} justify="flex-end"> */}
                {/* <Button variant="outlined"></Button> */}
                {/* </Grid> */}
                {/* </Grid> */}

                <Grid container>&nbsp;</Grid>
                <Grid item xs={12}>
                  <TextField
                    id="outlined-multiline-static"
                    fullWidth
                    // size="small"
                    label=""
                    multiline
                    rows={4}
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <Grid item xs={1}>
                &nbsp;
              </Grid>
            </Grid>
            <Grid container>&nbsp;</Grid>
          </Paper>
        </Grid>
      </Container>
    </div>
  );
};

export default R053Main;
