import React from "react";
import { Container, Box, Button } from "@material-ui/core";
import { IconButton } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import DeleteIcon from "@material-ui/icons/Delete";

// material-ui
// button

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  webButton: {
    textTransform: "none",
  },
  input: {
    display: "none",
  },
}));

/**
 * 
 */
const R010Main = () => {
  const classes = useStyles();

  return (
    <div>
      <Container maxWidth="sm" className="bg-c-lightyellow">
        {/* <p>R010Main.js</p> */}
        <Box component="span" m={1}>
          <Button variant="contained">Default</Button>
        </Box>
        <Box component="span" m={1}>
          <Button variant="contained" disableElevation>
            
          </Button>
        </Box>

        <hr />

        <Box component="span" m={1}>
          <Button variant="contained" color="primary">
            primary
          </Button>
        </Box>
        <Box component="span" m={1}>
          <Button
            variant="contained"
            color="primary"
            className={classes.webButton}
          >
            primary 
          </Button>
        </Box>

        <hr />

        <p className="m22">outline</p>
        <Button variant="outlined">Outlined</Button>
        <Button
          onClick={() => {
            alert("clicked");
          }}
        >
          !
        </Button>

        <hr />

        <p className="m22">uploadhtmlinput</p>
        <input
          accept="image/*"
          className={classes.input}
          id="contained-button-file"
          multiple
          type="file"
        />
        <label htmlFor="contained-button-file">
          <Button variant="contained" color="primary" component="span">
            Upload
          </Button>
        </label>

        <hr />

        <p className="m22"></p>
        <Button
          variant="contained"
          size="small"
          color="primary"
          className={classes.margin}
        >
          Small
        </Button>
        <Button
          variant="contained"
          size="medium"
          color="primary"
          className={classes.margin}
        >
          Medium
        </Button>
        <Button
          variant="contained"
          size="large"
          color="primary"
          className={classes.margin}
        >
          Large
        </Button>

        <hr />

        <p className="m22">linka</p>
        <Button color="primary">text button</Button>
        <Button href="#text-buttons" color="primary">
          Link
        </Button>

        <hr />

        <p className="m22"></p>
        <IconButton aria-label="delete">
          <DeleteIcon /> 
        </IconButton>
        <Button
          variant="contained"
          color="secondary"
          className={classes.button}
          startIcon={<DeleteIcon />}
        >
          Delete
        </Button>

        <hr />
      </Container>
    </div>
  );
};

export default R010Main;
