import React, { useState } from "react";
import { Container, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
import FavoriteIcon from "@material-ui/icons/Favorite";

// msgboard mock

/**
 * style
 */
const useStyles = makeStyles((theme) => ({
  good_btn: {
    border: "1px solid pink",
    padding: "1px 1px",
    color: "pink",
    fontWeight: 600,
    // fontSize: "16px",
    minWidth: "45px",
  },
  uname: {
    fontWeight: 600,
  },
  thread_title: {
    backgroundColor: "#5b5b5b",
    color: "white",
    borderRadius: "4px 4px 4px 4px",
    paddingLeft: "0.5em",
  },
  msgs_list: {
    // backgroundColor: "#5b5b5b",
    paddingLeft: "1em",
    // color: "#363636",
    color: "#474747",
  },
  msg_title: {
    // backgroundColor: "#5b5b5b",
    marginTop: "2px",
    marginBottom: "2px",
  },
  input_panel: {
    paddingLeft: "1.2em",
    paddingRight: "1.2em",
  },
  footer_container: {
    height: "5em",
    backgroundColor: "#FAFAFA",
    color: "gray",
  },
}));

/**
 * 
 */
const R054Main = () => {
  // style
  const classes = useStyles();

  // good
  const [good, setGood] = useState(0);

  /**
   * Good
   */
  const handleGoodClick = () => {
    setGood(good + 1);
  };

  return (
    <div>
      <Container fixed className=" ">
        {/* <Typography variant="h5" className="m22">
          msgboard mock
        </Typography>
        <hr /> */}

        {/*  */}
        <Grid container className={classes.thread_title}>
          <Typography variant="subtitle1">23</Typography>
        </Grid>

        {/*  */}
        <Grid container>
          <ol className={classes.msgs_list}>
            <li className={classes.msg_title}>
              <Typography variant="body2">
                <Typography variant="inherit" className={classes.uname}>
                  
                </Typography>
                2020/08/21 23:04:15 &nbsp;
                <Button
                  variant="outlined"
                  color="secondary"
                  size="small"
                  className={classes.good_btn}
                  onClick={handleGoodClick}
                >
                  <FavoriteIcon />
                  &nbsp;{good}
                </Button>
              </Typography>
            </li>
            <Typography variant="body2">
              980
              
              980
               
              BBS
            </Typography>
            <li className={classes.msg_title}>
              <Typography variant="body2">
                <Typography variant="inherit" className={classes.uname}>
                  
                </Typography>
                2020/08/21 23:04:15 &nbsp;
                <Button
                  variant="outlined"
                  color="secondary"
                  size="small"
                  className={classes.good_btn}
                  onClick={handleGoodClick}
                >
                  <FavoriteIcon />
                  &nbsp;{good}
                </Button>
              </Typography>
            </li>
            <Typography variant="body2">
              
              
            </Typography>
          </ol>
        </Grid>

        {/*  */}
        <Grid item xs={12}>
          <Paper className={classes.input_panel}>
            <Grid container>&nbsp;</Grid>
            <Grid container>
              <TextField
                fullWidth
                size="small"
                label=""
                placeholder=""
                variant="outlined"
              />
              <Grid container>&nbsp;</Grid>
              <TextField
                fullWidth
                // size="small"
                label=""
                multiline
                rows={4}
                variant="outlined"
              />
            </Grid>
            <Grid container>&nbsp;</Grid>
            <Grid container justify="flex-end">
              <Button variant="outlined" size="small">
                
              </Button>
            </Grid>
            <Grid container>&nbsp;</Grid>
          </Paper>
        </Grid>

        {/* footer */}
        <Grid
          container
          justify="center"
          alignItems="center"
          className={classes.footer_container}
        >
          &copy; Copyright 2021 Hoge Corp.
        </Grid>
      </Container>
    </div>
  );
};

export default R054Main;
