import React, { useContext } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Button } from "@material-ui/core";
import FavoriteIcon from "@material-ui/icons/Favorite";
import { ApiMBContext } from "../../context/ApiMBContext";

/**
 * style
 */
const useStyles = makeStyles((theme) => ({
  good_btn: {
    border: "1px solid pink",
    padding: "1px 1px",
    color: "pink",
    fontWeight: 600,
    // fontSize: "16px",
    minWidth: "50px",
  },
}));

/**
 * Good
 * @param {*} props
 */
const GoodBtn = (props) => {
  // style
  const classes = useStyles();
  // context
  const { apiPutMsg } = useContext(ApiMBContext);
  // msg
  const msg = props.msg;

  /**
   * Good
   */
  const handleGoodClick = () => {
    // TODO:goodadd
    // add
    msg.good += 1;
    // PUT
    apiPutMsg(msg);
  };

  return (
    <>
      <Button
        variant="outlined"
        color="secondary"
        size="small"
        className={classes.good_btn}
        onClick={handleGoodClick}
      >
        <FavoriteIcon />
        &nbsp;{msg.good}&nbsp;
      </Button>
    </>
  );
};

export default GoodBtn;
