import React, { useContext, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Container } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { ApiMBContext } from "../../context/ApiMBContext";
import MsgList from "./MsgList";
import MsgInputField from "./MsgInputField";
// import TestComponent from "./TestComponent";

/**
 * style
 */
const useStyles = makeStyles((theme) => ({
  thread_title: {
    backgroundColor: "#5b5b5b",
    color: "white",
    borderRadius: "4px 4px 4px 4px",
    paddingLeft: "0.5em",
  },
  footer_container: {
    height: "5em",
    backgroundColor: "#FAFAFA",
    color: "gray",
  },
}));

const R100MBMain = () => {
  // style
  const classes = useStyles();
  // useContext
  const { apiGetMsgs } = useContext(ApiMBContext);

  /**
   * 
   */
  useEffect(() => {
    // msg
    apiGetMsgs();
  }, []);

  return (
    <div>
      <Container fixed>
        {/*  */}
        <Grid container className={classes.thread_title}>
          <Typography variant="subtitle1">23</Typography>
        </Grid>

        {/*  */}
        <MsgList />

        {/*  */}
        <MsgInputField />

        {/* footer */}
        <Grid
          container
          justify="center"
          alignItems="center"
          className={classes.footer_container}
        >
          &copy; Copyright 2021 Hoge Corp.
        </Grid>
      </Container>

      {/*  */}
      {/* <TestComponent /> */}
    </div>
  );
};

export default R100MBMain;
