import React from "react";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { Button } from "@material-ui/core";
import { IconButton } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import TextField from "@material-ui/core/TextField";
import { blue } from "@material-ui/core/colors";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Box from "@material-ui/core/Box";
import Tooltip from "@material-ui/core/Tooltip";
import { withStyles } from "@material-ui/core/styles";

// material-ui
// BookList

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  header: {
    // backgroundColor: "blue",
  },
  title: {
    flexGrow: 1,
  },
  h02: {
    // width: "100%",
    // paddingLeft: "1em",
    // margin: 0,
    // border: "2 solid",
    // border: "1",
    // backgroundColor: "blue",
    // borderRadius: 3,
    height: "100vh",
  },
  bookPaper: {
    width: "100%",
    paddingLeft: "1em",
  },
  btnBookAdd: {
    marginRight: "0.4em",
  },
}));

/**
 * tooltip()
 */
const LightTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: theme.palette.common.white,
    color: "rgba(0, 0, 0, 0.87)",
    boxShadow: theme.shadows[1],
    fontSize: "1em",
  },
}))(Tooltip);

/**
 * 
 */
const R035Grid = () => {
  const classes = useStyles();

  function createData(title, author, memo) {
    return { title: title, author: author, memo: memo };
  }

  const bookRows = [
    createData(
      "",
      "",
      ""
    ),
    createData("", "", ""),
    // createData("", "", ""),
    // createData("", "", ""),
    // createData("", "", ""),
    // createData("", "", ""),
    // createData("", "", ""),
    // createData("", "", ""),
    // createData("", "", ""),
    // createData("", "", ""),
    createData("", "", ""),
    createData(
      "",
      "",
      ""
    ),
  ];

  return (
    <div>
      <Container fixed className="">
        {/*  */}
        <AppBar position="static">
          <Toolbar variant="dense">
            <Typography variant="h5" color="inherit" className={classes.title}>
              
            </Typography>
            <Button color="inherit"></Button>
          </Toolbar>
        </AppBar>

        {/*  */}
        <Paper elevation={1} className={classes.h02}>
          <Grid container className="" justify="center">
            <Typography variant="h6" color="inherit">
              
            </Typography>
            {/* </Box> */}
          </Grid>

          {/* BookList */}
          <Grid container className="">
            {bookRows.map((item, idx) => {
              return (
                <Paper elevation={3} className={classes.bookPaper} key={idx}>
                  <Grid item container alignItems="center">
                    {/*  */}
                    <LightTooltip title={item.title} placement="top">
                      <Grid item xs={3} className="">
                        <div
                          style={{
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            width: "100%",
                            textOverflow: "ellipsis",
                          }}
                        >
                          {item.title}
                        </div>
                      </Grid>
                    </LightTooltip>

                    {/*  */}
                    <Grid item xs={3} className="">
                      <div
                        style={{
                          whiteSpace: "nowrap",
                          overflow: "hidden",
                          width: "100%",
                          textOverflow: "ellipsis",
                        }}
                      >
                        {item.author}
                      </div>
                    </Grid>

                    {/*  */}
                    <Grid item xs={5} className="">
                      <TextField fullWidth value={item.memo} />
                    </Grid>

                    {/*  */}
                    <Grid item container xs={1} className="" justify="center">
                      <IconButton
                        aria-label="delete"
                        // size="small"
                        size="medium"
                      >
                        <DeleteIcon />
                      </IconButton>
                    </Grid>
                  </Grid>
                </Paper>
              );
            })}
          </Grid>
        </Paper>
      </Container>
    </div>
  );
};

export default R035Grid;
