import React, { useState } from "react";
import { Container, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Switch from "@material-ui/core/Switch";
import { ControlCamera } from "@material-ui/icons";
import TextField from "@material-ui/core/TextField";
import AccountCircle from "@material-ui/icons/AccountCircle";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import FavoriteIcon from "@material-ui/icons/Favorite";
import NavigationIcon from "@material-ui/icons/Navigation";
import Badge from "@material-ui/core/Badge";
import MailIcon from "@material-ui/icons/Mail";
import IconButton from "@material-ui/core/IconButton";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import { pink } from "@material-ui/core/colors";

// material-ui
// form

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  good_btn: {
    border: "1px solid pink",
    padding: "1px 1px",
    color: "hotpink",
    fontWeight: 600,
  },
  icon_pink: {
    color: "deeppink",
    // backgroundColor: "pink",
  },
}));

const StyledBadge = withStyles((theme) => ({
  badge: {
    right: -3,
    top: 13,
    border: `2px solid ${theme.palette.background.paper}`,
    padding: "0 4px",
    color: "white",
    backgroundColor: "black",
  },
}))(Badge);

/**
 * 
 */
const R051Main = () => {
  const classes = useStyles((theme) => ({}));

  return (
    <div>
      <Container fixed className="bg-c-lightyellow height200vh">
        <h3 className="m22"></h3>
        <hr />
        <div className={classes.root}>
          <Fab color="primary" aria-label="add">
            <AddIcon />
          </Fab>
          <Fab color="secondary" aria-label="edit">
            <EditIcon />
          </Fab>
          <Fab variant="extended">
            <NavigationIcon className={classes.extendedIcon} />
            Navigate
          </Fab>
          <Fab disabled aria-label="like">
            <FavoriteIcon />
          </Fab>
          <Fab size="small" color="primary" aria-label="add">
            <FavoriteIcon />
          </Fab>
        </div>
        <hr />
        <div className={classes.root}>
          <Badge badgeContent={4} color="primary">
            <MailIcon />
          </Badge>
          <Badge badgeContent={4} color="secondary">
            <MailIcon />
          </Badge>
          <Badge badgeContent={4} color="error">
            <MailIcon />
          </Badge>
          <IconButton aria-label="cart">
            <StyledBadge badgeContent={4} color="secondary">
              <ShoppingCartIcon />
            </StyledBadge>
          </IconButton>
          <Badge badgeContent={4} color="error">
            <ShoppingCartIcon />
          </Badge>
          <hr />
          <IconButton>
            <Badge badgeContent={4} color="error">
              <MailIcon color="secondary" />
            </Badge>
          </IconButton>
          <IconButton>
            <Badge badgeContent={4} color="primary">
              <FavoriteIcon color="secondary" />
            </Badge>
          </IconButton>
          <IconButton>
            <Badge badgeContent={4} color="error">
              <FavoriteIcon className={classes.icon_pink} />
            </Badge>
          </IconButton>
          <hr />
          <Fab size="small" color="secondary" aria-label="add">
            <Badge badgeContent={4} color="error">
              <FavoriteIcon />
            </Badge>
          </Fab>
          <Fab variant="extended" color="secondary">
            <FavoriteIcon />
            &nbsp;{5}
          </Fab>
          <Fab variant="extended" color="secondary" size="small">
            <FavoriteIcon />
            &nbsp;{5}
          </Fab>
          <Fab variant="round" color="secondary" size="small">
            <FavoriteIcon />
            &nbsp;{5}
          </Fab>
          <hr />
          <Button variant="outlined" color="primary">
            Outlined
          </Button>
          <Button variant="outlined" color="secondary">
            <FavoriteIcon />
            &nbsp;{5}
          </Button>
          <Button variant="outlined" color="secondary" size="small">
            <FavoriteIcon />
            &nbsp;{5}
          </Button>
          <hr />
          1.  2019/08/21 () 23:04:15&nbsp;
          <Button variant="outlined" color="secondary" size="small">
            <FavoriteIcon />
            &nbsp;{5}
          </Button>
          <hr />
          1.  2019/08/21 () 23:04:15&nbsp;
          <Button
            variant="outlined"
            color="secondary"
            size="small"
            className={classes.good_btn}
          >
            <FavoriteIcon />
            &nbsp;{5}
          </Button>
          <hr />
          1.&nbsp;2020/08/21 23:04:15&nbsp;
          <Button
            variant="outlined"
            color="secondary"
            size="small"
            className={classes.good_btn}
          >
            <FavoriteIcon />
            &nbsp;{5}
          </Button>
          <hr />
          1.&nbsp;2019/08/21 23:04:15&nbsp;
          <IconButton>
            <Badge badgeContent={4} color="error">
              <FavoriteIcon className={classes.icon_pink} />
            </Badge>
          </IconButton>
        </div>
      </Container>
    </div>
  );
};

export default R051Main;
