import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Hidden, IconButton } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import TextField from "@material-ui/core/TextField";
import Box from "@material-ui/core/Box";

// Table
// BookList
// 

const useStyles = makeStyles({
  table: {
    minWidth: 400,
    // backgroundColor: "#ffffff",
    tbody: {
      backgroundColor: "black",
    },
    // TableBody: {
    //   // backgroundColor: "red",
    //   backgroundColor: "black",
    //   TableCell: {
    //     backgroundColor: "black",
    //   },
    // },
  },
  cell: {
    // backgroundColor: "black",
    backgroundColor: "aqua",
    // overflow: Hidden,
    // overflow-x: hidden !important;
    // overflowX: "",
    // textOverflow: "hidden",
  },
  cellAuth: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    // width: 100,
    // width: "100px",
    // width: "10%",
    textOverflow: "ellipsis",
  },
});

function createData(title, author, memo) {
  return { title: title, author: author, memo: memo };
}

const rows = [
  createData(
    "",
    "",
    ""
  ),
  createData("", "", ""),
  createData("", "", ""),
];

const R063Table = () => {
  const classes = useStyles();

  return (
    <div>
      <TableContainer component={Paper}>
        <Table
          className={classes.table}
          size="small"
          aria-label="a dense table"
        >
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.title}>
                <TableCell align="left" width="30%" className={classes.cell}>
                  {/* <span overflow="hidden">{row.name}</span> */}
                  {/* <div style={{ width: 200, whiteSpace: "nowrap" }}> */}
                  <div style={{ width: 100, whiteSpace: "nowrap" }}>
                    {/* <div style={{ width: "100", whiteSpace: "nowrap" }}> */}
                    <Box
                      // component="div"
                      // my={2}
                      overflow="hidden"
                      // bgcolor="background.paper"
                    >
                      Overflow Hidden. Overflow Hidden. Overflow Hidden.
                    </Box>
                  </div>
                </TableCell>
                <TableCell
                  align="left"
                  width="20%"
                  // className={classes.cellAuth}
                >
                  {/* {row.calories} */}
                  <div
                    style={{
                      whiteSpace: "nowrap",
                      overflow: "hidden",
                      width: 100,
                      // maxWidth: 50,
                      textOverflow: "ellipsis",
                    }}
                  >
                    {row.author}
                  </div>
                </TableCell>
                <TableCell align="left" width="40%">
                  <TextField id="standard-basic" fullWidth value={row.memo} />
                </TableCell>
                <TableCell align="center" width="10%">
                  <IconButton
                    aria-label="delete"
                    // size="medium"
                    size="small"
                  >
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default R063Table;
