import React from "react";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

// material-ui
// gried layout

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  parent: {
    height: "200px",
  },
  cell_1: {
    height: "30px",
    width: "60%",
  },
  cell_2: {
    height: "40px",
    width: "80%",
  },
  cell_3: {
    height: "50px",
    width: "100%",
  },
}));

/**
 * 
 */
const R031Main = () => {
  const classes = useStyles();

  return (
    <div>
      <Container fixed className="bg-c-lightyellow">
        <p className="m22"> </p>
        <Grid container className={classes.parent}>
          <Grid item xs={2}>
            <Paper className={classes.cell_1}>cell_1</Paper>
          </Grid>
          <Grid item xs={2}>
            <Paper className={classes.cell_2}>cell_2</Paper>
          </Grid>
          <Grid item xs={2}>
            <Paper className={classes.cell_3}>cell_3</Paper>
          </Grid>
        </Grid>
        <hr />

        <p className="m22"></p>
        <Grid
          container
          className={classes.parent}
          // direction="column"
          justify="center"
          alignItems="center"
        >
          <Grid item xs={2}>
            <Paper className={classes.cell_1}>cell_1</Paper>
          </Grid>
          <Grid item xs={2}>
            <Paper className={classes.cell_2}>cell_2</Paper>
          </Grid>
          <Grid item xs={2}>
            <Paper className={classes.cell_3}>cell_3</Paper>
          </Grid>
        </Grid>
        <hr />

        <p className="m22"></p>
        <Grid
          container
          className={classes.parent}
          direction="column"
          justify="center"
          alignItems="center"
        >
          <Grid item xs={2}>
            <Paper className={classes.cell_1}>cell_1</Paper>
          </Grid>
          <Grid item xs={2}>
            <Paper className={classes.cell_2}>cell_2</Paper>
          </Grid>
          <Grid item xs={2}>
            <Paper className={classes.cell_3}>cell_3</Paper>
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default R031Main;
