import React from "react";
import { colors, Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

// material-ui
// gried layout

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  parent: {
    // height: "200px",
    border: "solid 1px",
  },
  parent2: {
    height: "200px",
    // border: "solid 1px",
    backgroundColor: "skyblue",
  },
  parent3: {
    height: "200px",
    // border: "solid 1px",
    backgroundColor: "aqua",
  },
  cell_1: {
    height: "30px",
    width: "60%",
  },
  cell_2: {
    height: "40px",
    width: "80%",
  },
  cell_3: {
    height: "50px",
    width: "100%",
  },
}));

/**
 * 
 */
const R032Main = () => {
  const classes = useStyles();

  return (
    <div>
      <Container fixed className="bg-c-lightyellow">
        <Grid container>
          <Grid item xs={6}>
            <p className="m22"></p>
          </Grid>
          <Grid item xs={6}>
            <p className="m22"></p>
          </Grid>
        </Grid>
        <Grid container>
          <Grid
            item
            xs={6}
            container
            className={classes.parent}
            justify="center"
            alignItems="center"
          >
            <Grid item xs={2}>
              <Paper className={classes.cell_1}>cell_1</Paper>
            </Grid>
            <Grid item xs={2}>
              <Paper className={classes.cell_2}>cell_2</Paper>
            </Grid>
            <Grid item xs={2}>
              <Paper className={classes.cell_3}>cell_3</Paper>
            </Grid>
          </Grid>

          <Grid item xs={6} className={classes.parent}>
            <Grid
              item
              container
              direction="column"
              className={classes.parent2}
              justify="space-around"
              alignItems="center"
            >
              <Grid item xs={2}>
                <Paper className={classes.cell_1}>cell_1</Paper>
              </Grid>
              <Grid item xs={2}>
                <Paper className={classes.cell_2}>cell_2</Paper>
              </Grid>
              <Grid item xs={2}>
                <Paper className={classes.cell_3}>cell_3</Paper>
              </Grid>
            </Grid>
            <Grid
              item
              container
              // direction="column"
              className={classes.parent3}
              justify="space-around"
              alignItems="center"
            >
              <Grid item xs={2}>
                <Paper className={classes.cell_1}>cell_1</Paper>
              </Grid>
              <Grid item xs={2}>
                <Paper className={classes.cell_2}>cell_2</Paper>
              </Grid>
              <Grid item xs={2}>
                <Paper className={classes.cell_3}>cell_3</Paper>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default R032Main;
