import React from "react";

/**
 * 1
 */
function Square(props) {
  return (
    <button className="square" onClick={props.handleClick}>
      {props.value}
    </button>
  );
}

/**
 * 
 */
class Board extends React.Component {
  /**
   * Square
   * @param {*} i
   */
  renderSquare(i) {
    return (
      <Square
        value={this.props.squares[i]}
        handleClick={() => {
          this.props.handleClick(i);
        }}
      />
    );
  }

  render() {
    return (
      // 
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}

/**
 * Game Main
 */
export default class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // 
      history: [
        {
          // 9
          squares: Array(9).fill(null),
        },
      ],
      // 
      stepNumber: 0,
      // 
      xIsNext: true,
    };
  }

  /**
   * 
   * @param {*} i
   */
  handleClick(i) {
    // console.log("handelClick", i);

    // history
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    // 
    const current = history[history.length - 1];
    // 
    // stateslice()
    const squares = current.squares.slice();

    //  or return
    if (calculateWinner(squares) || squares[i]) {
      return;
    }

    // 
    squares[i] = this.state.xIsNext ? "X" : "O";
    this.setState({
      // history
      // concat
      history: history.concat([
        {
          squares: squares,
        },
      ]),
      // 
      stepNumber: history.length,
      // 
      xIsNext: !this.state.xIsNext,
    });
  }

  /**
   * Go to 
   * @param {*} move
   */
  jumpto(step) {
    // console.log("jumpto", step);

    // 
    this.setState({
      stepNumber: step,
      xIsNext: step % 2 === 0,
    });
  }

  render() {
    // 
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = calculateWinner(current.squares);

    /**
     * Go to 
     */
    const moves = history.map((step, move) => {
      const desc = move ? `Go to Move # ${move}` : "Go to Move # start";
      return (
        <li key={move}>
          <button onClick={() => this.jumpto(move)}>{desc}</button>
        </li>
      );
    });

    // txt
    // txt
    let txtStatus = "";
    if (winner) {
      txtStatus = `Winner: ${winner}`;
    } else {
      txtStatus = `Next player: ${current.xIsNext ? "X" : "O"}`;
    }

    return (
      <div className="game">
        <div className="game-board">
          {/*  */}
          <Board
            squares={current.squares}
            handleClick={(i) => this.handleClick(i)}
          />
        </div>
        <div className="game-info">
          {/*  */}
          <div>{txtStatus}</div>
          {/*  */}
          <div>stepNumber: {this.state.stepNumber}</div>
          {/* Go to  */}
          <ol>{moves}</ol>
        </div>
      </div>
    );
  }
}

/**
 * 
 * @param {*} squares
 * @retunr X:X O:O null:
 */
function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}

