import React, { useContext } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { ApiMBContext } from "../../context/ApiMBContext";
import MsgItem from "./MsgItem";

/**
 * style
 */
const useStyles = makeStyles((theme) => ({
  msgs_list: {
    paddingLeft: "1em",
    color: "#474747",
  },
}));

/**
 * 
 */
const MsgList = () => {
  // style
  const classes = useStyles();
  // useContext
  const { msgs } = useContext(ApiMBContext);

  return (
    <div>
      <Grid container>
        <ol className={classes.msgs_list}>
          {msgs.map((item, idx) => {
            return <MsgItem key={idx} item={item} />;
          })}
        </ol>
      </Grid>
    </div>
  );
};

export default MsgList;
