import React from "react";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

// material-ui
// gried layout

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  webButton: {
    textTransform: "none",
  },
  input: {
    display: "none",
  },
}));

/**
 * 
 */
const R030Main = () => {
  const classes = useStyles();

  return (
    <div>
      <Container fixed className="bg-c-lightyellow">
        <p className="m22">Grid Normal</p>
        <Grid container>
          <Grid item xs={6} className="bg-c-sky">
            xs=6
          </Grid>
          <Grid item xs={6} className="bg-c-pink">
            xs=6
          </Grid>
        </Grid>
        <hr />
        <Grid container>
          <Grid item xs={3} className="bg-c-sky">
            xs=3
          </Grid>
          <Grid item xs={3} className="bg-c-pink">
            xs=3
          </Grid>
          <Grid item xs={3} className="bg-c-sky">
            xs=3
          </Grid>
          <Grid item xs={3} className="bg-c-pink">
            xs=3
          </Grid>
        </Grid>
        <hr />

        <Grid container spacing={3}>
          <p className="m22">break</p>
          <Grid item xs={12}>
            <Paper className={classes.paper}>xs=12</Paper>
          </Grid>
          <Grid item xs={6}>
            <Paper className={classes.paper}>xs=6</Paper>
          </Grid>
          <Grid item xs={6}>
            <Paper className={classes.paper}>xs=6</Paper>
          </Grid>
          <Grid item xs={3}>
            <Paper className={classes.paper}>xs=3</Paper>
          </Grid>
          <Grid item xs={3}>
            <Paper className={classes.paper}>xs=3</Paper>
          </Grid>
          <Grid item xs={3}>
            <Paper className={classes.paper}>xs=3</Paper>
          </Grid>
          <Grid item xs={3}>
            <Paper className={classes.paper}>xs=3</Paper>
          </Grid>
        </Grid>
        <hr />

        <Grid container spacing={3}>
          <p className="m22">break</p>
          <Grid item xs={12}>
            <Paper className={classes.paper}>xs=12</Paper>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Paper className={classes.paper}>xs=12 sm=6</Paper>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Paper className={classes.paper}>xs=12 sm=6</Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Paper className={classes.paper}>xs=6 sm=3</Paper>
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default R030Main;
