import React from "react";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";

// material-ui
// container

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
  webButton: {
    textTransform: "none",
  },
  input: {
    display: "none",
  },
}));

/**
 * 
 */
const R020Main = () => {
  const classes = useStyles();

  return (
    <div>
      <Container maxWidth="sm">
        <Typography
          component="div"
          style={{ backgroundColor: "#cfe8fc", height: "30vh" }}
        >
          maxWidth="sm"
          <br />
          <br />
          xs, extra-small: 0px
          <br />
          sm, small: 600px
          <br />
          md, medium: 960px
          <br />
          lg, large: 1280px
          <br />
          xl, extra-large: 1920px
        </Typography>
        <hr />
      </Container>
      <Container fixed>
        <Typography
          component="div"
          style={{ backgroundColor: "#cfe8fc", height: "20vh" }}
        >
          fixed 
        </Typography>
      </Container>
    </div>
  );
};

export default R020Main;
