import React, { useState, useContext } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { Button } from "@material-ui/core";
import { ApiMBContext } from "../../context/ApiMBContext";
import TextField from "@material-ui/core/TextField";
import * as utils from "../../utils/utils";

/**
 * style
 */
const useStyles = makeStyles((theme) => ({
  input_panel: {
    paddingLeft: "1.2em",
    paddingRight: "1.2em",
  },
}));

/**
 * 
 */
const MsgInputField = () => {
  // style
  const classes = useStyles();
  // context
  const { apiPostMsg } = useContext(ApiMBContext);
  // 
  const [msgInput, setMsgInput] = useState({ name: "", text: "" });

  /**
   * 
   */
  const handleNameChange = () => (event) => {
    const value = event.target.value;
    setMsgInput({ ...msgInput, name: value });
  };

  /**
   * 
   */
  const handleTextChange = () => (event) => {
    const value = event.target.value;
    setMsgInput({ ...msgInput, text: value });
  };

  /**
   * 
   */
  const handleSend = () => {
    // 
    const msg = {
      key: utils.getRandStr(),
      name: msgInput.name,
      text: msgInput.text ? msgInput.text : "",
      date: utils.getFormatTime(),
      good: 0,
    };
    // POST
    apiPostMsg(msg);
    // 
    setMsgInput({ name: "", text: "" });
  };

  return (
    <div>
      <Grid item xs={12}>
        <Paper className={classes.input_panel}>
          <Grid container>&nbsp;</Grid>
          <Grid container>
            {/*  */}
            <TextField
              fullWidth
              size="small"
              label=""
              placeholder=""
              variant="outlined"
              value={msgInput.name}
              onChange={handleNameChange()}
            />
            <Grid container>&nbsp;</Grid>

            {/*  */}
            <TextField
              fullWidth
              label=""
              placeholder=""
              multiline
              rows={4}
              variant="outlined"
              value={msgInput.text}
              onChange={handleTextChange()}
            />
          </Grid>
          <Grid container>&nbsp;</Grid>
          <Grid container justify="flex-end">
            {/*  */}
            <Button variant="outlined" size="small" onClick={handleSend}>
              
            </Button>
          </Grid>
          <Grid container>&nbsp;</Grid>
        </Paper>
      </Grid>
    </div>
  );
};

export default MsgInputField;
