import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import IconButton from "@material-ui/core/IconButton";
// import EBData from "./evangelionBookData.json";
import Grid from "@material-ui/core/Grid";
import { Container } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { AddCircleOutline, SearchOutlined } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: "100%",
    height: 300,
  },
  icon: {
    color: "rgba(255, 255, 255, 0.6)",
  },
}));

/**
 * 
 */
const R083GridList = () => {
  const classes = useStyles();
  // 
  const [sWord, setSWord] = useState({ title: "", author: "" });
  // 
  const [books, setBooks] = useState([]);

  /**
   * 
   * @param {*} books
   */
  const extractBooks = (books) => {
    const items = books.items;
    const result = items.map((item) => {
      return {
        title: item.volumeInfo.title,
        author: item.volumeInfo.authors
          ? item.volumeInfo.authors.join(", ")
          : "",
        img: item.volumeInfo.imageLinks
          ? item.volumeInfo.imageLinks.smallThumbnail
          : "",
      };
    });
    return result;
  };

  // ()
  // const bookList = extractBooks(EBData);

  /**
   * 
   * @param {*} e
   */
  const handleSearch = (e) => {
    console.log("handleSearch");
    searchExec(sWord.title, sWord.author, 20);
  };

  const handleTitle = (e) => {
    setSWord({ ...sWord, title: e.target.value });
  };
  const handleAuthor = (e) => {
    setSWord({ ...sWord, author: e.target.value });
  };

  /**
   * GETURL
   * @param title
   * @param author
   * @param maxResults
   * @returns
   */
  function buildSearchUrl(title, author, maxResults) {
    // base url
    let url = "https://www.googleapis.com/books/v1/volumes?q=";
    const conditions = [];
    if (title) {
      // 
      conditions.push(`intitle:${title}`);
    }
    if (author) {
      // 
      conditions.push(`inauthor:${author}`);
    }
    return url + conditions.join("+") + `&maxResults=${maxResults}`;
  }

  /**
   * API
   * 
   */
  // useEffect(() => {
  const searchExec = (title, author, maxResults) => {
    // if (isSearching) {
    // URL
    const url = buildSearchUrl(title, author, maxResults);
    console.log("url", url);

    // API
    fetch(url)
      .then((res) => {
        return res.json();
      })
      .then((json) => {
        return extractBooks(json);
      })
      .then((books) => {
        setBooks(books);
        console.log(books);
      })
      .catch((err) => {
        console.error(
          "APIGoogleBooks"
        );
        console.error(err);
      });
    // }
    // 
    // setIsSearching(false);
  };

  /**
   * 
   */
  const searchJSX = (
    <Grid container>
      <Grid item xs={8}>
        <div>
          <TextField
            label=""
            variant="outlined"
            size="small"
            fullWidth
            value={sWord.title}
            onChange={(e) => handleTitle(e)}
          />
        </div>
        <div style={{ marginTop: 8 }}>
          <TextField
            label=""
            variant="outlined"
            size="small"
            fullWidth
            value={sWord.author}
            onChange={(e) => handleAuthor(e)}
          />
        </div>
      </Grid>
      <Grid item container xs={4} justify="flex-end">
        <div>
          <Button
            variant="outlined"
            size="large"
            onClick={(e) => {
              handleSearch(e);
            }}
          >
            <SearchOutlined /> 
          </Button>
        </div>
      </Grid>
    </Grid>
  );

  /**
   * List
   */
  const bookListJSX = (
    <Grid container>
      <GridList cellHeight={100} className={classes.gridList}>
        <GridListTile
          key="Subheader"
          cols={2}
          style={{ height: "auto" }}
        ></GridListTile>
        {books.map((tile, idx) => (
          <GridListTile key={idx.toString()}>
            <img src={tile.img} alt={tile.title} />
            <GridListTileBar
              title={tile.title}
              subtitle={<span>{tile.author}</span>}
              actionIcon={
                <IconButton
                  aria-label={`info about ${tile.title}`}
                  className={classes.icon}
                >
                  <AddCircleOutline />
                </IconButton>
              }
            />
          </GridListTile>
        ))}
      </GridList>
    </Grid>
  );

  return (
    <div className={classes.root}>
      <Container fixed className="">
        <div>API Call</div>
        {/*  */}
        {searchJSX}
        {/* List */}
        {bookListJSX}
      </Container>
    </div>
  );
};

export default R083GridList;
