import React from "react";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import { Button } from "@material-ui/core";
import { IconButton } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import TextField from "@material-ui/core/TextField";

// material-ui
// BookList

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  title: {
    margin: 2,
    marginLeft: "0.2em",
  },
  btnBookAdd: {
    marginRight: "0.4em",
  },
}));

/**
 * 
 */
const R034Grid = () => {
  const classes = useStyles();

  function createData(title, author, memo) {
    return { title: title, author: author, memo: memo };
  }

  const bookRows = [
    createData(
      "",
      "",
      ""
    ),
    createData("", "", ""),
    createData(
      "",
      "",
      ""
    ),
  ];

  return (
    <div>
      <Container fixed className="bg-c-lightyellow">
        {/*  */}
        <Grid container>
          <Grid item container xs={8} className="bg-c-sky">
            <h1 className={classes.title}></h1>
          </Grid>
          <Grid
            item
            container
            xs={4}
            className="bg-c-pink"
            direction="row"
            justify="flex-end"
            alignItems="center"
          >
            <Button className={classes.btnBookAdd} variant="outlined">
              
            </Button>
          </Grid>
        </Grid>

        {/*  */}
        <Grid container className="bg-c-sky" justify="center">
          <h2 className="m22"></h2>
        </Grid>

        {/* List */}
        <Grid container className="bg-c-sky">
          {bookRows.map((item, idx) => {
            return (
              <Grid item container key={idx} alignItems="center">
                {/*  */}
                <Grid item xs={3} className="bg-c-sky">
                  <div
                    style={{
                      whiteSpace: "nowrap",
                      overflow: "hidden",
                      width: "100%",
                      textOverflow: "ellipsis",
                    }}
                  >
                    {item.title}
                  </div>
                </Grid>

                {/*  */}
                <Grid item xs={3} className="bg-c-pink">
                  <div
                    style={{
                      whiteSpace: "nowrap",
                      overflow: "hidden",
                      width: "100%",
                      textOverflow: "ellipsis",
                    }}
                  >
                    {item.author}
                  </div>
                </Grid>

                {/*  */}
                <Grid item xs={5} className="bg-c-sky">
                  <TextField id="standard-basic" fullWidth value={item.memo} />
                </Grid>

                {/*  */}
                <Grid
                  item
                  container
                  xs={1}
                  className="bg-c-pink"
                  justify="center"
                >
                  <IconButton
                    aria-label="delete"
                    size="small"
                    // size="medium"
                  >
                    <DeleteIcon />
                  </IconButton>
                </Grid>
              </Grid>
            );
          })}
        </Grid>
      </Container>
    </div>
  );
};

export default R034Grid;
