import React from "react";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { Button } from "@material-ui/core";

// material-ui
// BookList

/**
 * 
 */
const useStyles = makeStyles((theme) => ({
  title: {
    margin: 2,
    marginLeft: "0.2em",
  },
  btnBookAdd: {
    marginRight: "0.5em",
  },
}));

/**
 * 
 */
const R033Grid = () => {
  const classes = useStyles();

  return (
    <div>
      <Container fixed className="bg-c-lightyellow">
        {/*  */}
        <Grid container>
          <Grid item container xs={9} className="bg-c-sky">
            <h1 className={classes.title}></h1>
          </Grid>
          <Grid
            item
            container
            xs={3}
            className="bg-c-pink"
            direction="row"
            justify="flex-end"
            alignItems="center"
          >
            <Button className={classes.btnBookAdd} variant="outlined">
              
            </Button>
          </Grid>
        </Grid>

        {/*  */}
        <Grid container className="bg-c-sky" justify="center">
          <h2 className="m22"></h2>
        </Grid>

        {/* List */}
        <Grid container>
          <Grid item xs={3} className="bg-c-sky">
            xs=3
          </Grid>
          <Grid item xs={2} className="bg-c-pink">
            xs=3
          </Grid>
          <Grid item xs={5} className="bg-c-sky">
            xs=3
          </Grid>
          <Grid item xs={2} className="bg-c-pink">
            xs=3
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default R033Grid;
