import React, { createContext, useState, useEffect, useContext } from "react";
// import { withCookies } from "react-cookie";
import axios from "axios";
import { MenuItem } from "@material-ui/core";

export const ApiMBContext = createContext();

/**
 * API Endpoint access
 * @param {*} props
 */
const MsgBoardContextProvider = (props) => {
  // 
  const [testCount, setTestCount] = useState(0);
  // 
  const [msgs, setMsgs] = useState([]);

  /**
   * 
   */
  useEffect(() => {
    // console.log("MsgBoardContextProvider useEffect start...");
  }, []);

  /**
   * 
   */
  const addTestCount = () => {
    // console.log("addTestCount start...");
    let _cnt = testCount;
    _cnt += 1;
    setTestCount(_cnt);
  };

  /**
   * API GET
   * msg
   */
  const apiGetMsgs = async () => {
    try {
      // API
      const res = await axios.get("http://127.0.0.1:8000/api/thmsgs/", {
        headers: {},
      });
      // 
      setMsgs(res.data);
    } catch (error) {
      // 
      // TODO: 
      console.log("error apiGetMsgs");
      console.log(error);
    }
  };

  /**
   * API GET
   * msg1
   * Promise
   * @param {*} id
   */
  const apiGetMsgOne = async (id) => {
    try {
      // API
      const res = await axios.get(`http://127.0.0.1:8000/api/thmsgs/${id}`, {
        headers: {},
      });
      // 
      // Promise
      //   console.log("res", res);
      //   console.log("res.data", res.data);
      //   return res.data;
      return res;
    } catch (error) {
      // 
      // TODO: 
      console.log("error apiGetMsgOne");
      console.log(error);
    }
  };

  /**
   * API POST
   * msg1
   * @param {*} item
   */
  const apiPostMsg = async (item) => {
    try {
      // API post 
      const res = await axios.post("http://127.0.0.1:8000/api/thmsgs/", item, {
        headers: {
          "Content-Type": "application/json",
        },
      });
      // 
      setMsgs([...msgs, res.data]);
    } catch (error) {
      // 
      // TODO: 
      console.log("error apiPostMsg");
      // 
      if (error.response) {
        console.log(error.response.data);
        console.log(error.response.status); // 400
        console.log(error.response.statusText); // Bad Request
        console.log(error.response.headers);
      } else if (error.request) {
        console.log(error.request);
      } else {
        console.log("Error", error.message);
      }
      console.log(error.config);
      console.log(error);
    }
  };

  /**
   * API PUT
   * msg1
   */
  const apiPutMsg = async (item) => {
    try {
      // API put 
      const res = await axios.put(
        `http://127.0.0.1:8000/api/thmsgs/${item.id}/`,
        item,
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
      // 
      msgs.map((_mitem, idx) => {
        if (_mitem.id === res.data.id) {
          msgs[idx] = res.data;
        }
      });
      setMsgs([...msgs]);
    } catch (error) {
      // 
      // TODO: 
      console.log("error apiPutMsg");
      // 
      if (error.response) {
        console.log(error.response.data);
        console.log(error.response.status); // 400
        console.log(error.response.statusText); // Bad Request
        console.log(error.response.headers);
      } else if (error.request) {
        console.log(error.request);
      } else {
        console.log("Error", error.message);
      }
      console.log(error.config);
      console.log(error);
    }
  };

  // componentstate
  return (
    <ApiMBContext.Provider
      value={{
        testCount,
        addTestCount,
        msgs,
        apiGetMsgs,
        apiGetMsgOne,
        apiPostMsg,
        apiPutMsg,
      }}
    >
      {props.children}
    </ApiMBContext.Provider>
  );
};

export default MsgBoardContextProvider;
